import React from 'react';
import PropTypes from 'prop-types';

import { Panel, Badge, Counter, PanelHeader, Header, TabbarItem, Button, Group, Tabbar, Cell, Div, Avatar, CellButton, Link, List, HeaderButton, platform, removeEventListener, PanelHeaderBack, FormItem, SliderSwitch, View } from '@vkontakte/vkui';

import persik from '../img/persik.png';
import './Load.css';

const Prof = ({id, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader
			left={<PanelHeaderBack onClick={id} data-to="home"/>}
		>
			Пополнить счет
		</PanelHeader>
		<img className="Persik" src={persik} alt="Persik The Cat"/>
		
     
        {
   <div>
   <div><center><h1>{"Error page"}</h1></center></div>
</div>};


    

                {fetchedUser &&
		<Group header={<Header mode="secondary" >Профиль</Header>}>
			<Cell
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</Cell>
		</Group>}


	</Panel>
)




Prof.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Prof;


